/* eslint-disable no-undef */
import { createContext, useState } from 'react';
import { Technician } from '../../shared/Types/Report.type';

export type TechnicianContexType = {
  technician: Technician;
  setTechnician: React.Dispatch<React.SetStateAction<Technician>>;
};

const initialContextState: TechnicianContexType = {
  technician: {}, // Initially, no tests are loaded
  setTechnician: () => {}, // Placeholder function, will be replaced by a real function in a provider
};

export const TechnicianContext =
  createContext<TechnicianContexType>(initialContextState);

type TechnicianProviderProps = {
  children: React.ReactNode;
};

export function TechnicianProvider({ children }: TechnicianProviderProps) {
  const [technician, setTechnician] = useState<Technician>({});

  return (
    <TechnicianContext.Provider
      // eslint-disable-next-line react/jsx-no-constructed-context-values
      value={{ technician, setTechnician }}
    >
      {children}
    </TechnicianContext.Provider>
  );
}
