/* eslint-disable no-undef */
import { createContext, useState } from 'react';
import { Device } from '../../shared/Types/Report.type';

export type DeviceContexType = {
  device: Device;
  setDevice: React.Dispatch<React.SetStateAction<Device>>;
};

const initialContextState: DeviceContexType = {
  device: {}, // Initially, no tests are loaded
  setDevice: () => {}, // Placeholder function, will be replaced by a real function in a provider
};

export const DeviceContext =
  createContext<DeviceContexType>(initialContextState);

type DeviceProviderProps = {
  children: React.ReactNode;
};

export function DeviceProvider({ children }: DeviceProviderProps) {
  const [device, setDevice] = useState<Device>({});

  return (
    <DeviceContext.Provider
      // eslint-disable-next-line react/jsx-no-constructed-context-values
      value={{ device, setDevice }}
    >
      {children}
    </DeviceContext.Provider>
  );
}
