/* eslint-disable no-undef */
import { createContext, useState } from 'react';
import { Test, Tests, TestStatus } from '../../shared/Types/Tests.type';

export type TestContextType = {
  tests: Tests;
  setTests: (tests: Test[]) => void;
  updateTestStatus: (testName: string, newStatus: TestStatus) => void;
  updateTestSelection: (testName: string, isSelect: boolean) => void;
};

const initialContextState: TestContextType = {
  tests: [], // Initially, no tests are loaded
  setTests: () => {}, // Placeholder function, will be replaced by a real function in a provider
  updateTestStatus: () => {}, // Placeholder function, will be replaced by a real function in a provider
  updateTestSelection: () => {}, // Placeholder function, will be replaced by a real function in a provider
};

export const TestsContext = createContext<TestContextType>(initialContextState);

type TestProviderProps = {
  children: React.ReactNode;
};

export function TestsProvider({ children }: TestProviderProps) {
  const [tests, setTests] = useState<Tests>([]);

  const updateTestStatus = (testName: string, newStatus: TestStatus) => {
    if (testName === 'All') {
      setTests((prevStatuses: Tests) =>
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        prevStatuses.map(([name, status, isSelect]) => [
          name,
          newStatus,
          isSelect,
        ])
      );
    } else {
      setTests((prevStatuses: Tests) =>
        prevStatuses.map(([name, status, isSelect]) =>
          name === testName
            ? [name, newStatus, isSelect]
            : [name, status, isSelect]
        )
      );
    }
  };

  const updateTestSelection = (testName: string, newSelect: boolean) => {
    if (testName === 'All') {
      setTests((prevStatuses: Tests) =>
        prevStatuses.map(([name, status]) => [name, status, newSelect])
      );
    } else {
      setTests((prevStatuses: Tests) =>
        prevStatuses.map(([name, status, isSelected]) =>
          name === testName
            ? [name, status, newSelect]
            : [name, status, isSelected]
        )
      );
    }
  };

  return (
    <TestsContext.Provider
      // eslint-disable-next-line react/jsx-no-constructed-context-values
      value={{ tests, setTests, updateTestStatus, updateTestSelection }}
    >
      {children}
    </TestsContext.Provider>
  );
}
