/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import { useCallback, useContext, useEffect } from 'react';
import { BrowserWindow, IpcRendererEvent } from 'electron';
import { TestResult, TestStatus } from '../../shared/Types/Tests.type';
import invokeServer from '../IPC/InvokeServer';
import { ErrorMessage } from '../../shared/Types/ErrorMessage';
import { offServerEvent, onServerEvent } from '../IPC/OnServerEvent';
import { TestContextType, TestsContext } from '../Context/Tests.context';
import { DeviceContexType, DeviceContext } from '../Context/Device.context';
import {
  TechnicianContexType,
  TechnicianContext,
} from '../Context/Technician.context';
import { Report, TestReport } from '../../shared/Types/Report.type';

export default function useServerEventHandlers() {
  const testContext = useContext<TestContextType>(TestsContext);
  const technicianContext = useContext<TechnicianContexType>(TechnicianContext);
  const deviceContext = useContext<DeviceContexType>(DeviceContext);

  const testResult = useCallback(
    (_event: IpcRendererEvent, payload: TestResult) => {
      const { testName, message, result } = payload;
      const { updateTestStatus } = testContext;
      updateTestStatus(
        testName,
        result ? TestStatus.Success : TestStatus.Failed
      );
    },
    []
  );

  const testInProgress = useCallback(
    (_event: IpcRendererEvent, testName: string) => {
      const { updateTestStatus } = testContext;
      updateTestStatus(testName, TestStatus.Progress);
    },
    []
  );

  const finishedTestsHandler = useCallback(
    (_event: IpcRendererEvent, payload: null) => {
      const testToReport: TestReport[] = testContext.tests
        .filter((test) => test[1] !== TestStatus.Waiting)
        .map((test) => {
          return {
            testName: test[0],
            testResult: test[1],
          };
        });

      const report: Report = {
        technician: technicianContext.technician,
        device: deviceContext.device,
        testReport: testToReport,
      };
      invokeServer('create_report', report);
    },
    []
  );

  const errorMessageHandler = useCallback(
    (_event: IpcRendererEvent, err: ErrorMessage) => {
      console.error(`Error from server: ${err.stringMessage}`);
    },
    []
  );

  useEffect(() => {
    onServerEvent('test_result', testResult, false);
    onServerEvent('test_in_progress', testInProgress, false);
    onServerEvent('error_message', errorMessageHandler, false);
    onServerEvent('finished_tests_alert', finishedTestsHandler, false);
    return () => {
      offServerEvent('test_result', testResult);
      offServerEvent('error_message', errorMessageHandler);
      offServerEvent('test_in_progress', testInProgress);
      offServerEvent('finished_tests_alert', finishedTestsHandler);
    };
  }, [testResult, errorMessageHandler]);
}
