import './App.css';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import useServerEventHandlers from './hooks/serverEventHandlers.hook';
import {
  UserActionHandlers,
  useUserActionHandlers,
} from './hooks/userActionHandlers.hook';
import WelcomePage from './pages/WelcomePage/WelcomePage.component';
import Background from './Components/Background/Background.component';
import TesterPage from './pages/TesterPage/TesterPage.component';
import AllProviders from './hooks/contextProviders.hook';

function AppContents() {
  // Need to pass the `userActionHandlers` to the relative components
  const userActionHandlers: UserActionHandlers = useUserActionHandlers();
  useServerEventHandlers();

  return (
    <Router>
      <Background />
      <Routes>
        <Route
          path="/"
          element={
            <WelcomePage CheckVersion={userActionHandlers.handleCheckVer} />
          }
        />
        <Route path="/tester" element={<TesterPage />} />
      </Routes>
    </Router>
  );
}

export default function App() {
  return (
    <AllProviders>
      <AppContents />
    </AllProviders>
  );
}
