/* eslint-disable no-undef */
import { Box, Grid, TextField } from '@mui/material';
import Numbers from '@mui/icons-material/Numbers';
import WorkIcon from '@mui/icons-material/Work';
import BadgeIcon from '@mui/icons-material/Badge';
import FlightIcon from '@mui/icons-material/Flight';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import { ChangeEvent, useContext } from 'react';
import { Item } from './DeviceDetails.styled';
import { DeviceContexType, DeviceContext } from '../../Context/Device.context';
import {
  TechnicianContexType,
  TechnicianContext,
} from '../../Context/Technician.context';

type TextFieldContainerProps = {
  // eslint-disable-next-line no-undef
  children: React.ReactNode;
};

function TextFieldContainer({ children }: TextFieldContainerProps) {
  return (
    <Grid item xs={1.5} sm={2.5} md={2.5}>
      <Item>
        <Box sx={{ display: 'flex', alignItems: 'flex-end' }}>{children}</Box>
      </Item>
    </Grid>
  );
}
function TextFieldBox({
  label,
  onChange,
}: {
  label: string;
  onChange: (event: ChangeEvent<HTMLInputElement>) => void;
}) {
  return (
    <TextField
      id="input-with-sx"
      label={label}
      variant="standard"
      dir="rtl"
      onChange={onChange}
      // inputProps={{ style: { fontSize: 15 } }} // font size of input text
      // InputLabelProps={{ style: { fontSize: 15, flexWrap: 'wrap' } }} // font size of input label
    />
  );
}

export default function DeviceDetails() {
  const deviceContext = useContext<DeviceContexType>(DeviceContext);
  const technicianContext = useContext<TechnicianContexType>(TechnicianContext);

  const { setDevice } = deviceContext;
  const { setTechnician } = technicianContext;

  const handleTechnicianDetails = (prop: string, value: string) => {
    setTechnician((currentTechnician) => ({
      ...currentTechnician,
      [prop]: value,
    }));
  };

  const handleDeviceDetails = (prop: string, value: string) => {
    setDevice((currentDevice) => ({
      ...currentDevice,
      [prop]: value,
    }));
  };

  return (
    <Grid
      container
      spacing={{ xs: 3, md: 4 }}
      columns={{ xs: 4, sm: 8, md: 12 }}
      style={{ justifyContent: 'center', flexWrap: 'nowrap' }}
    >
      <TextFieldContainer>
        <Numbers sx={{ color: 'action.active', mr: 1, my: 0.5 }} />
        <TextFieldBox
          label="Serial Number"
          onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
            handleDeviceDetails('serialNumber', event?.target.value)
          }
        />
      </TextFieldContainer>
      <TextFieldContainer>
        <WorkIcon sx={{ color: 'action.active', mr: 1, my: 0.5 }} />
        <TextFieldBox
          label="Paka"
          onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
            handleDeviceDetails('Paka', event?.target.value)
          }
        />
      </TextFieldContainer>
      <TextFieldContainer>
        <FlightIcon sx={{ color: 'action.active', mr: 1, my: 0.5 }} />
        <TextFieldBox
          label="IAF Number"
          onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
            handleDeviceDetails('IAFNumber', event?.target.value)
          }
        />
      </TextFieldContainer>
      <TextFieldContainer>
        <AccountCircleIcon sx={{ color: 'action.active', mr: 1, my: 0.5 }} />
        <TextFieldBox
          label="Name"
          onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
            handleTechnicianDetails('name', event?.target.value)
          }
        />
      </TextFieldContainer>
      <TextFieldContainer>
        <Box sx={{ display: 'flex', alignItems: 'flex-end' }}>
          <BadgeIcon sx={{ color: 'action.active', mr: 1, my: 0.5 }} />
          <TextFieldBox
            label="Personal Number"
            onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
              handleTechnicianDetails('personalNumber', event?.target.value)
            }
          />
        </Box>
      </TextFieldContainer>
    </Grid>
  );
}
