/* eslint-disable guard-for-in */
import { useContext } from 'react';
import invokeServer from '../IPC/InvokeServer';
import { TestContextType, TestsContext } from '../Context/Tests.context';
import { Test, TestStatus, Tests } from '../../shared/Types/Tests.type';
// Used to defined handlers to user request

export type UserActionHandlers = {
  handleCheckVer: () => Promise<boolean>;
  handleStartTest: () => void;
  handleOpenReportFolder: () => void;
  clearTests: () => void;
  getPlatform: () => Promise<string>;
};

export const useUserActionHandlers = (): UserActionHandlers => {
  const testContext = useContext<TestContextType>(TestsContext);

  const handleCheckVer = async () => {
    const testsNames = await invokeServer('check_ver', null);
    const testsArray: Tests | undefined = testsNames?.map((test) => [
      test,
      TestStatus.Waiting,
      true,
    ]);

    if (testsArray && testsArray.length !== 0) {
      testContext.setTests(testsArray);
      return Promise.resolve(true);
    }

    return Promise.resolve(false);
  };

  const clearTests = () => {
    const { updateTestStatus } = testContext;
    updateTestStatus('All', TestStatus.Waiting);
  };

  const handleStartTest = () => {
    clearTests();
    const testsToRun: string[] = testContext.tests
      ?.filter((test: Test) => test[2])
      .map((test: Test) => test[0]);
    invokeServer('start_tester', testsToRun);
  };

  const handleOpenReportFolder = () => {
    invokeServer('open_reports_folder', null);
  };

  const getPlatform = async () => {
    const platform = await invokeServer('get_platform', null);
    return Promise.resolve(platform);
  };

  return {
    handleCheckVer,
    handleStartTest,
    handleOpenReportFolder,
    clearTests,
    getPlatform,
  };
};
