import { useEffect, useState } from 'react';
import ControlPanel from '../../Components/ControlPanel/ControlPanel.component';
import FormalTitle from '../../Components/FormalTitle/FormalTitle.component';
import TestsTable from '../../Components/TestsTable/TestsTable.component';
import {
  UserActionHandlers,
  useUserActionHandlers,
} from '../../hooks/userActionHandlers.hook';
import { Container, OuterContainer } from './TesterPage.styles';

export default function TesterPage() {
  const userActionHandlers: UserActionHandlers = useUserActionHandlers();
  const [platform, setPlatform] = useState('');

  useEffect(() => {
    const platformHandler = async () => {
      setPlatform(await userActionHandlers.getPlatform());
    };

    platformHandler();
  });

  return (
    <OuterContainer>
      <FormalTitle text={`ECAROS - ${platform}`} />
      <Container>
        <ControlPanel
          StartTester={userActionHandlers.handleStartTest}
          OpenReportFolder={userActionHandlers.handleOpenReportFolder}
          ClearTests={userActionHandlers.clearTests}
        />
        <TestsTable />
      </Container>
    </OuterContainer>
  );
}
