/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable no-undef */
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Checkbox from '@mui/material/Checkbox';
import { useContext } from 'react';
import {
  StatusContainer,
  StyledTableCell,
  StyledTableRow,
} from './TestsTable.styles';
import { TestsContext, TestContextType } from '../../Context/Tests.context';
import {
  StatusColors,
  TestStatus,
  Test,
} from '../../../shared/Types/Tests.type';
import ProgressBar from '../ProgressBar/ProgressBar.component';

type RowProps = {
  test: Test;
  index: number;
  onSelectHandler: (testName: string, selection: boolean) => void;
};

function Row(props: RowProps) {
  const { test, index, onSelectHandler } = props;

  const name = test[0];
  const status = test[1];
  const isCheck = test[2];

  return (
    <StyledTableRow>
      <StyledTableCell align="right">
        <StatusContainer backgroundColor={StatusColors[status]}>
          {TestStatus[status]}
        </StatusContainer>
      </StyledTableCell>
      <StyledTableCell component="th" scope="row" align="center" dir="rtl">
        {name}
      </StyledTableCell>
      <StyledTableCell component="th" scope="row" align="center" dir="rtl">
        {index + 1}
      </StyledTableCell>
      <StyledTableCell align="center">
        <Checkbox
          checked={isCheck}
          onChange={(event) => onSelectHandler(name, event.target.checked)}
        />
      </StyledTableCell>
    </StyledTableRow>
  );
}

export default function TestsTable() {
  const testContext = useContext<TestContextType>(TestsContext);
  const { tests, updateTestSelection } = testContext;

  const onSelectHandler = (testName: string, isSelected: boolean) => {
    updateTestSelection(testName, isSelected);
  };

  const calculateProgressValue = () => {
    const selectedTests = tests.filter((test) => test[2]);
    if (selectedTests.length === 0) return 0;
    return (
      (selectedTests.filter((test) => test[1] !== TestStatus.Waiting).length *
        100) /
      selectedTests.length
    );
  };

  const isAllSelected = tests.every((test: Test) => test[2]);

  return (
    <div>
      <ProgressBar value={calculateProgressValue()} />
      <TableContainer
        component={Paper}
        style={{
          height: '50vh',
        }}
        sx={{
          overflow: 'auto',
          '&::-webkit-scrollbar': {
            width: '10px',
            borderRadius: '5px',
            backgroundColor: `rgba(255, 255, 255, 0.57)`,
          },
          '&::-webkit-scrollbar-thumb': {
            backgroundColor: `rgba(0, 0, 0, 0.47)`,
            borderRadius: '5px',
            border: '2px solid transparent',
            backgroundClip: 'content-box',
          },
          '&::-webkit-scrollbar-thumb:hover': {
            backgroundColor: `rgba(0, 0, 0, 0.25)`,
          },
        }}
      >
        <Table
          stickyHeader
          aria-label="customized table"
          sx={{
            height: 'max-content',
          }}
        >
          <TableHead>
            <TableRow>
              <StyledTableCell align="center">סטאטוס</StyledTableCell>
              <StyledTableCell align="center">שם בדיקה</StyledTableCell>
              <StyledTableCell align="center">מספר בדיקה</StyledTableCell>
              <StyledTableCell align="center">
                <Checkbox
                  onChange={(event) =>
                    onSelectHandler('All', event.target.checked)
                  }
                  checked={isAllSelected}
                />
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {tests.map((test, index) => {
              return (
                <Row
                  key={test[0]}
                  test={test}
                  index={index}
                  onSelectHandler={onSelectHandler}
                />
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
