/* eslint-disable react-hooks/exhaustive-deps */
import { useNavigate } from 'react-router-dom';
import { Container, Title, VersionButton } from './WelcomePage.styles';

type WelcomePageProps = {
  CheckVersion: () => Promise<boolean>;
};

export default function WelcomePage(prop: WelcomePageProps) {
  const { CheckVersion } = prop;
  const navigate = useNavigate();

  const HandleCheckVerClick = async () => {
    const isRecognizedPlatform = await CheckVersion();
    if (isRecognizedPlatform) navigate('/tester');
  };

  return (
    <Container>
      <Title>Ecaros Tester</Title>
      <VersionButton variant="contained" onClick={HandleCheckVerClick}>
        Check Version
      </VersionButton>
    </Container>
  );
}
