import { Button } from '@mui/material';
import DeviceDetails from '../DeviceDetails/DeviceDetails.component';
import { ButtonsContainer, Container } from './ControlPanel.styles';

type ControlPanelProps = {
  StartTester: () => void;
  OpenReportFolder: () => void;
  ClearTests: () => void;
};

export default function ControlPanel(props: ControlPanelProps) {
  const { StartTester, OpenReportFolder, ClearTests } = props;

  return (
    <Container>
      <DeviceDetails />
      <ButtonsContainer>
        <Button variant="contained" onClick={StartTester}>
          Start
        </Button>
        <Button variant="contained" onClick={ClearTests}>
          Clear
        </Button>
        <Button variant="contained" onClick={OpenReportFolder}>
          Open report folder
        </Button>
      </ButtonsContainer>
    </Container>
  );
}
